<script>
  import { onMount } from 'svelte'

  onMount(() => {
    document.addEventListener('show-toast', (e) => {
      console.log('test')
      let ctx = document.querySelector('.snackbar')
      let ctxText = document.querySelector('.snackbar-title')
      let ctxIcon = document.querySelector('.snackbar-icon')
      let snackbarProps = e.detail
      
      ctxText.innerHTML = snackbarProps.title
      

      if (!ctx.classList.contains('active')) {    
        ctxIcon.classList.add(snackbarProps.icon)    
        ctx.animate({
          opacity: [0, 1]
        },
        {
          duration: 200,
          iterations: 1,
          easing: "ease-out"
        }).onfinish = (e) => {
          e.target.effect.target.style.opacity = 1;
          ctx.classList.add('active')
        }

        setTimeout(() => {
          ctx.animate({
            opacity: [1, 0]
          },
          {
            duration: 200,
            iterations: 1,
            easing: "ease-in"
          }).onfinish = (e) => {
            e.target.effect.target.style.opacity = 0;
            ctxIcon.classList.remove(snackbarProps.icon)
            ctx.classList.remove('active')
            
          }
        }, e.detail.time)
      }


    })
  })


</script>

<div class="snackbar">
  <div class="snackbar-wrapper d-flex">
    <i class="snackbar-icon icon"></i>
    <span class="snackbar-title"></span>
  </div>
</div>

<style>
  .snackbar {
    background-color: #3e3e4b;
    margin-left: -125px;
    text-align: center;
    border-radius: 3px;
    padding: 8px 15px;
    min-width: 250px;
    position: fixed;
    /* font-size: 17px; */
    color: #fff;
    bottom: 20px;
    right: 20px;
    z-index: 1;
    opacity: 0;
  }

  .snackbar .snackbar-wrapper {
    justify-content: space-between;
    align-items: center;
  }

</style>