import { wrap } from 'svelte-spa-router/wrap'

export const userPref = '/user'
export const userRouters = {
  '/': wrap({
    asyncComponent: () => import('../page/User/page/DashboardPage.svelte')
  }),
  '/mytransact': wrap({
    asyncComponent: () => import('../page/User/page/TransactionPage.svelte')
  }),
  '/myaccount': wrap({
    asyncComponent: () => import('../page/User/page/AccountPage.svelte')
  }),
  '*': wrap({
    asyncComponent: () => import('../page/User/page/NotFoundPage.svelte')
  })
}

export const adminPref = '/admin'
export const adminRouters = {
  '/': wrap({
    asyncComponent: () => import('../page/Admin/page/DashboardPage.svelte')
  }),
  '/transact': wrap({
    asyncComponent: () => import('../page/Admin/page/TransactionListPage.svelte')
  }),
  '/account': wrap({
    asyncComponent: () => import('../page/Admin/page/AccountPage.svelte')
  }),
  '*': wrap({
    asyncComponent: () => import('../page/Admin/page/NotFoundPage.svelte')
  })
}

export default {
  '/': wrap({
    asyncComponent: () => import('../page/Login/LoginPage.svelte')
  }),
  '/admin': wrap({
    asyncComponent: () => import('../page/Admin/AdminPage.svelte')
  }),
  '/admin/*': wrap({
    asyncComponent: () => import('../page/Admin/AdminPage.svelte')
  }),
  '/user': wrap({
    asyncComponent: () => import('../page/User/UserPage.svelte')
  }),
  '/user/*': wrap({
    asyncComponent: () => import('../page/User/UserPage.svelte')
  }),
  '/logout': wrap({
    asyncComponent: () => import('../page/Login/LoginPage.svelte')
  }),
  '*': wrap({
    asyncComponent: () => import('../page/NotFound/NotFoundPage.svelte')
  })
}